
* {
  margin: 0;
  padding: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


:root {
  --page-black: rgb(2, 0, 0);
  --page-white: rgb(245, 247, 248);
  --page-grey: rgb(78, 78, 78);
  --page-yellow: rgb(255, 255, 4);
}

.welcome{
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: var(--page-black);
  z-index: 3900;
  color: rgb(214, 200, 5);
  cursor: none;
  transition: all 2s ease-in-out;
  overflow: hidden;
}

.welcomeText {
  /* padding: 20% 33%; */
  position: absolute;
  top:45%;
  left: 25%;
  font-size: 5rem;
  /* animation: Welcome 4s ease-in forwards; */

}



.welcomeText path:nth-child(2){
  stroke-dasharray: 572;
  stroke-dashoffset: 572;
  animation: textRoll 2s ease-in forwards;

}
.welcomeText path:nth-child(3){
  stroke-dasharray: 463;
  stroke-dashoffset: 463;
  animation: textRoll 2s ease-in forwards ;

}
.welcomeText path:nth-child(4){
  stroke-dasharray: 495;
  stroke-dashoffset: 495;
  animation: textRoll 2s ease-in forwards ;

}
.welcomeText path:nth-child(5){
  stroke-dasharray: 467;
  stroke-dashoffset: 467;
  animation: textRoll 2s ease-in forwards ;

}
.welcomeText path:nth-child(6){
  stroke-dasharray: 635;
  stroke-dashoffset: 635;
  animation: textRoll 2s ease-in forwards;

}
.welcomeText path:nth-child(7){
  stroke-dasharray: 604;
  stroke-dashoffset: 604;
  animation: textRoll 2s ease-in forwards;

}
.welcomeText path:nth-child(8){
  stroke-dasharray: 463;
  stroke-dashoffset: 463;
  animation: textRoll 2s ease-in forwards;

}

.welcomeText path:nth-child(9){
  stroke-dasharray: 587;
  stroke-dashoffset: 567;
  animation: textRoll 2s ease-in forwards;

}


@keyframes Welcome {
  to {
    transform: scale(0%);
    opacity: 0;
    
  }

  50%{
    transform: scale(100%);
    opacity: 1;
  }

  100%{
    transform: scale(0%);
    opacity: 0;
  }
}

@keyframes textRoll {

  0% {
    fill: transparent;

  }

  50%{
    fill: transparent;
  }

  80%{
    fill: transparent;
  }

  90%{
    fill: transparent;
  }

  95%{
    fill: transparent;
  }

  97%{
    fill: transparent;
  }

  99%{
    fill: transparent;
  }

  100% {
    fill: rgb(255, 217, 0);
    stroke-dashoffset: 0;

  }
}



.resume {
  color: var(--page-yellow);
}

body {
  /* background: url(./img/harvilla.jpg); */
  background-color: var(--page-black) ;
  background-repeat: no-repeat;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: var(--page-white);
  /* cursor: none; */
  overflow-x: hidden;

}

::-webkit-scrollbar{
  width: 5px;
  border-radius: 5px;
}

::-webkit-scrollbar-track{
  background-color: transparent;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background: var(--page-grey);
  height: 5px;
  border-radius: 5px;

}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--page-yellow);

}


.cursor {
  width: 10px;
  height: 10px;
  border: 1px solid var(--page-white);
  /* border-radius: 50%; */
  position: absolute;
  transition-duration:250ms;
  transition-timing-function: ease-out;
  animation: cursorAnim .5s infinite linear;
  z-index: 100;
  background-color: var(--page-white);
  
}

.cursor2 {
  width: 60px;
  height: 60px;
  position: absolute;
  border: 2px solid var(--page-white);
  border-radius: 50%;
  opacity: .6;
  /* top: -14px;
  left: 14px; */
  animation: cursorAnim2 .5s infinite alternate;
  transition-duration:250ms;
  transition-timing-function: ease-out;
  z-index: 100;
}

/* .cursor::after {
  content: "";
  width: 60px;
  height: 60px;
  position: absolute;
  border: 2px solid var(--page-white);
  border-radius: 50%;
  opacity: .6;
  top: -12px;
  left: -12px;
  animation: cursorAnim2 .5s infinite alternate !important;
} */

.expand {
  animation: cursorAnim3 .3s forwards;
  border: 6px solid var(--page-yellow);
  border-radius: 50%;
}

@keyframes cursorAnim {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes cursorAnim2 {
  from {
    transform: scale(.8);
  }

  to {
    transform: scale(1);
  }
}

@keyframes cursorAnim3 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(3);
  }

  100% {
    transform: scale(1);
    opacity: 0;
  }
}




.header {
  /* background-color: #2e2d2d9c; */
  width: 100%;
  height: 15%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  
  
}

.header {
  width: 100%;
}

.webTitle {
  margin-left: 130px;
}


.nav-content {
  display: none;
  width: 0;
  height: 0;
}

.nav {
  display: flex;
  list-style: none;
  margin-right: 200px ;
}

.nav-item {
  margin-right: 50px;
  padding-bottom: 8px;
  margin-top: 10px;
  cursor: pointer;
  position: relative;
  font-size: 18px;
}

.nav-item::before{
  content: "";
  position: absolute;
  top: 25px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--page-yellow);
  transform: scaleX(0);
  transform-origin: center;
  transition: transform .5s;
}

.nav-item:hover::before {
  transform: scaleX(1);
}


.active  {
  border-bottom: 3px solid var(--page-yellow);
  padding-bottom: 3.4px;
}

.main {
  /* transform: translateY(250px); */
  height: 655px;
  font-size: 18px;
  font-weight: 300;
  color: var(--page-white);
  background-image: url(./img/lightning.jpg) ;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}

.main_text {
  transform: translateX(150px);
  max-width: 900px;
  padding:200px 0px 100px 0;

}

.main p {
  line-height: 1.5;
  max-width: 50%;
  margin-top: 10px;
}

.inner_main {
  margin-top: 60px;
  font-size: 40px;
  background: var(--page-white);
  -webkit-text-fill-color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  text-shadow: 0px 0px solid 2px rgba(255, 255, 255, 0.377);
}


.btn {
  display: none;
  background-color: red;
  outline: none;
  padding: 15px 20px;
  border: solid 2px var(--page-white);
  border-radius: 60px;
  color: var(--page-white);
  font-size: 18px;
  font-weight: 500; 
  z-index: 500;
  width: 90px;
}

.btn:hover {
  background-image: linear-gradient(to right bottom, rgba(250, 236, 45, 0.233),rgb(124, 124, 119),rgba(245, 230, 20, 0.103));
  color: var(--page-black);
  transition: all 2s;
  cursor: pointer;
}

.sectionOne {
  display: block;
  margin-left: 150px;
  padding-top: 50px;
}

.sectionOne h5 {
  margin-bottom: 20px;
  color: var(--page-grey);
}

.sectionOne h2 {
  margin-bottom: 40px;
}

.projects {
  margin:30px 0px;
  font-size: 15px;
  width: 100%;
}

.project_text {
  text-align: center;
  color: var(--page-black);
}

.projectsHeader {
  margin-bottom: 20px;
}

.project_btn {
  outline: none;
  border: 1px solid var(--page-black);
  background-color: transparent;
  border-radius: 60px;
  display: flex;
  justify-self: center;
  color: var(--page-black);
}

.project_btn a {
  color: var(--page-black);
  text-decoration: none;
}

.project_btn:hover {
  background-color: rgb(139, 255, 110);
  box-shadow: 0px 0px 5px var(--page-white), 0px 0px 10px var(--page-white),0px 0px 15px var(--page-white), 2px 0px 20px var(--page-white) ;
  border: 1px solid rgba(255, 255, 255, 0.719);
  transition: all .3s;
}

.project_code {
  margin: 100px auto 0 auto;
  padding: 10px 30px;
}


.project_live {
  margin: 20px auto;
  padding: 10px 35px;
}

.project_image {
  max-width: 90%;
  margin-left: 16px;
  margin-top: 20px;
  border-radius: 5px;
}

.project_wrapper {
  display: grid;
  max-width: 88%;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}



.project_card {
  perspective: 150rem;
  perspective-origin: left;
  position: relative;
  height: 20rem; 
  z-index: 300;
}

.card_side {
  height: 20rem;
  transition: all 1s ease;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  backface-visibility: hidden;
  border-radius: 5px;
}

.card_side-front-a {
  background: linear-gradient(to bottom left, rgb(238, 179, 103), rgba(10, 239, 255, 0.637));
}

.card_side-back-a {
  background: linear-gradient(to top right, rgb(97, 223, 255), rgba(0, 255, 128, 0.692));
  transform: rotateY(180deg);


}

.card_side-front-b {
  background: linear-gradient(to left, rgb(243, 129, 234), rgb(93, 198, 247));
}

.card_side-back-b {
  background: linear-gradient(to bottom , rgb(178, 219, 243), rgb(64, 255, 46));
  transform: rotateY(180deg);


}

.card_side-front-c {
  background: linear-gradient(to bottom , rgb(253, 144, 20), rgb(148, 223, 125));
}

.card_side-back-c {
  background: linear-gradient(to bottom right, rgba(253, 4, 149, 0.726), rgb(182, 226, 81));
  transform: rotateY(180deg);
}


.project_card:hover .card_side-front-a {
  transform: rotateY(-180deg);
}

.project_card:hover .card_side-back-a {
  transform: rotateY(0);
}

.project_card:hover .card_side-front-b {
  transform: rotateY(-180deg);
}

.project_card:hover .card_side-back-b {
  transform: rotateY(0);
}

.project_card:hover .card_side-front-c {
  transform: rotateY(-180deg);
}

.project_card:hover .card_side-back-c {
  transform: rotateY(0);
}


.item_text {
  transition: all .9s;
  perspective: 15000px;
  /* -webkit-perspective: 15000px; */
}

.project_item:hover .item_text {
  transform: rotateY(180deg);
}

.item_1 {
  background-image: linear-gradient(to bottom left, rgb(127, 194, 221), rgb(177, 112, 177));
}

.item_2 {
  background-image: linear-gradient(to top right, rgb(231, 141, 219), rgb(143, 165, 118));
}

.item_3 {
  background-image: linear-gradient(to left, rgb(127, 194, 221), rgb(218, 150, 176));
}

.item_4 {
  background-image: linear-gradient(to bottom right, rgb(181, 255, 179), rgb(177, 112, 177));
}

.item_5 {
  background-image: linear-gradient(to bottom, rgb(179, 116, 116), rgb(93, 207, 252));
}

.item_6 {
  background-image: linear-gradient(to top, rgb(22, 23, 24), rgba(223, 148, 223, 0.76));
}

.sectionTwo {
  margin-left: 150px;
  margin-top: 100px;

}

.sectionTwo h3 {
  margin-bottom: 30px;
  color:var(--page-grey);
}

.connection {
