}
.twitter {
  border-bottom: 3px solid #1da1f2;
  padding: 30px;
}

.instagram {
  border-bottom: 3px solid #c13584;
  padding: 30px;
}

.LinkedIn {
  border-bottom: 3px solid #2867b2;
  padding: 30px;
}

.git {
  border-bottom: 3px solid #fafafa;
  padding: 30px;
}

.gmail {
  border-bottom: 3px solid #e92c1b;
  padding: 30px;
}

.sectionThree {
  margin-left: 150px;

}

.sectionThree h4 {
  color: var(--page-grey)
}

.aboutMe {
  display: flex;
  margin-top: 20px;
}
.img {
  /* filter: grayscale(100%); */
  width: 50%;
  height: 50%;
  margin-right: 50px;
  border-radius: 10px;
}



.aboutText {
  font-size: 18px;
  width: 500px;
  text-align: left;
  line-height: 1.8;
  margin-top: 0px;
  margin-left: 5px;
}

.aboutText h3 {
