}

.contact-box  {
  margin-top: 60px;
  display: flex;
  justify-content: space-between;
  width: 70%;
  color: var(--page-grey);
  margin-bottom: 60px;
  z-index: 300;
}

.contact-box h5 {
  margin-bottom: 20px;

}

.contact_link {
  font-size: 30px;
  color: var(--page-white);
  text-decoration: none;
}

.contact-email {
  margin-right: 150px;
}

.svg {
  width: 20px;
  background-color:var(--page-grey);
}

.footer {
  color: var(--page-grey);
  margin-bottom: 30px;
  text-align: center;
}

.tab_project {
  display: none;
}

.mobile_nav {
  display: none;
}

.mobile_side_nav {
  display: none;
}

.closeBtn {
  display: none;
}

