  .aboutText h3 {
    font-size: 18px;
  }

  .aboutText p {
    font-size: 13px;
  }

  .img {
    /* filter: grayscale(100%); */
    width: 100%;
    border-radius: 5px;
    margin: 0;
    margin-left: 15px;
    margin-top: 0px;
  }

  .aboutMe {
    display: grid;
    grid-template-rows: 200px 1fr;
    grid-row-gap: 20px;
    margin-top: 0px;
  }
  
  .btn {
    background-color: transparent;
    outline: none;
    padding: 10px 15px;
    border: 2px solid var(--page-white);
    border-radius: 60px;
    color: var(--page-white);
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    display: block;
  }

  .mobile_nav {
    display: block;
    position: fixed;
    right: 20px;
    top: 20px;
    width: 35px;
    height: 35px;
    text-align: center;
    background-color: white;
    border-radius: 50px;
    font-size: 10px;
    z-index: 1900;
  }

  .mobile_nav span{
    width: 5px;
    height: 5px;
    background-color: black;
    display: block;
    position: absolute;
    top: 15px;
    left: 15px;
    list-style: none;
    border-radius: 50%;
  }

  .mobile_nav span::before{
    content: "";
    width: 5px;
    height: 5px;
    background-color: black;
    display: block;
    position: absolute;
    list-style: none;
    top: -9px;
    border-radius: 50%;

  }

  .mobile_nav span::after{
    content: "";
    width: 5px;
    height: 5px;
    background-color: black;
    display: block;
    position: absolute;
    list-style: none;
    top: 9px;
    border-radius: 50%;

  }

  .mobile_side_nav {
    height: 100%; 
    width: 100%;
    position: fixed; 
    z-index: 2000; 
    top: 0;
    left: 0;
    background-color: #111; 
    color: white;
    overflow-x: hidden; 
    padding-top: 60px; 
    transition: 0.5s; 
    display: block;
  }

  .close_mobile_nav {
    display: inline-block;
    position: fixed;
    right: 20px;
    top: 20px;
    width: 35px;
    height: 35px;
    text-align: center;
    background-color: white;
    border-radius: 50px;
    font-size: 10px;
    z-index: 9900;
  }
  
  
  .close_mobile_nav span::before{
    content: "";
    width: 4px;
    height: 25px;
    background-color: black;
    display: block;
    position: absolute;
    top: 5px;
    left: 15px;
    border-radius: 3px;
    transform: rotate(-45deg);
  }

  .close_mobile_nav span::after{
    content: "";
    width: 4px;
    height: 25px;
    background-color: black;
    display: block;
    position: absolute;
    top: 5px;
    left: 15px;
    border-radius: 3px;
    transform: rotate(45deg);

  }
  
  .closeBtn {
    width: 35px;
    height: 35px;
    background-color: white;
    display: block;
    position: absolute;
    top: 30px;
    right: 20px;
    list-style: none;
    border-radius: 50%;
    font-size: 40px;
    color: black;
    text-align: center;
    z-index: 5000;
  }

  .icon {
    position: relative;
    top: -8px;
  }

  .nav_list {
    color: white;
    text-decoration: none;
    display: flex;
    margin-left: 30%;
    font-size: 40px;
    margin-top: 6px;
    
  }

  .navs {
    margin-top: 60%;
  }

  .footer {
    font-size: 10px;
  }
}